                    // creCount: 1, //符合信用的数量
                },
                person:[], //人员
                zhuanchaType: "",
                qualRecord: "", //资质备案
                projectSource: "all", //业绩来源 //全国资质与重庆资质筛选框,all为全国,chongq为重庆;
                personRecord: "not",//人员备案
                zhuanchaType: "company"
            },
            checked: false,
            checkList: [],
            peopleList: [], //人员列表;
            total: "", //查找数量
            codeZZ: "", //传给子组件的资质列表;
            isyj: false,
            isNoSee: true,
            isoptType: false,
            pkid: "", //查询id;
            chongqi: true, //是否是重庆专查
        };
    },
    watch: {
        'data.project':{
            deep:true,
            handler(newval,oldVal){
                if(newval.keywords!=''||newval.opt!='title'||newval.childProject
                    ||newval.proWhere||newval.proUse||newval.proType||newval.amountStart
                    ||newval.amountEnd||newval.contractStart||newval.contractEnd||newval.completeStart
                    ||newval.completeEnd||newval.areaStart||newval.areaEnd){//如果筛选了业绩，则显示符合业绩数量
                        this.isyj=true;
                }else{
                    this.isyj=false;
                    this.data.project.proCount = 0;
                }
                let arr=newval.keywords.split(' ');
                if(arr.length>1){
                    this.isoptType=true
                }else{
                    this.isoptType=false
                }
                this.data.project.proCount=1;
            }
        },
        data:{
            deep:true,
            handler(newVal,oldVal){
                if(JSON.stringify(newVal)==this.dataStr){
                    this.isNoSee=true;
                    return
                }
                this.isNoSee=false;
                this.companyCount();
            }
        },
        inject: ['reload'],
    },
    methods: {
        companyCount() {
            let data=JSON.parse(JSON.stringify(this.data));
            if(data.project.keywords&&data.project.keywords!=''){
                data.project.keywords=data.project.keywords.replace(/ /g,',');
            }else{
                data.project.opt=''
            }
            if(!this.isoptType){
                data.project.optType=''
            }
            if(!this.isyj){
                data.project.proCount=''
            }
            data.project=this.filterParams(data.project);
            data.credit = this.filterParams(data.credit);
            data = this.filterParams(data);
            if(this.current == 1) {
                screenConut(data).then(res => {
                    if(res.code == '1') {
                        const {count, pkid } = res.data;
                        this.total = count;
                        this.pkid = pkid;
                    }else {
                        console.info(res.code);
                    }
                })
            }else {
                this.data.zhuanchaType = "person";
                data.project.proCount= "1";
                personConut(data).then(res => {
                    // if(res.code == '1') {
                    //     this.total = res.data.count;
                    // }else {
                    //     console.info(res.code);
                    // }
                })
            }
        },
        //从子组件获取值;
        getCodeZZ(data) {
            this.data.qualCode = data;
        },
        getCodeRY(data) {
            this.data.person = data;
        },
        getRecordZZ(data) {
            if(data) {
                this.data.qualRecord = "yes";
            }else {
                this.data.qualRecord = "not";
            }
        },
        getRecordRY(data) {
            if(data) {
                this.data.personRecord = "yes";
            }else {
                this.data.personRecord = "not";
            }
        },
        getCreditQuery () {
            
        },
        // getCodeRY
        handleClick(index) {
            this.current = index;
            this.companyCount();
        },
        handleClickZZ (index) {
            this.currentZZ = index;
            if(index == 1) {
                this.data.project.optType = "or";
            }else {
                this.data.project.optType = "and";
            }
        },
        handleSelect (data) {
            this.data.project.opt = data.id;
        },
        changetable() {
        },
        backSelect(arr){//选不限时，其他取消选择
            for(let x of arr){
                if(x.areaShortName!='不限'){
                    x.istap=false
                }
            }
        },
        selectFn(el,arr){//选择
            if(el.areaShortName=='不限'){
                el.istap=true
                this.backSelect(arr)
            }else{
                el.istap=!el.istap
                if(this.forInLength(arr)){
                    arr[0].istap=true
                }else{
                    arr[0].istap=false
                }
            }
        },
        forInLength(arr){//其他都没选，自动选不限
            let a=[];
            for(let x of arr){
                if(x.istap){
                    a.push(x.areaShortName);
                }
            }
            if(a.length == 0){
                return true;
            }else{
                return false;
            }
        },
        forArrStr(arr){//从数组中取出对应值拼接
            let a=[];
            let str=''
            for(let x of arr){
                if(x.istap){
                    if(x.areaShortName=='不限'){
                        str=null
                        return str
                    }
                    a.push(x.areaShortName)
                }
            }
            str=a.join(',')
            return str
        },
        selectArea (item) {
            this.data.joinRegion = item.id;
        },
        itemFn(el){//业绩所含子项
            el.istap=!el.istap;
            this.data.project.childProject=this.forArrStr(this.itemList);
        },
        projectTap(el){//项目属地
            this.selectFn(el,this.projectList);
            this.data.project.proWhere=this.forArrStr(this.projectList);
        },
        purposeTap(el){//工程用途
            this.selectFn(el,this.purposeList)
            this.data.project.proUse=this.forArrStr(this.purposeList);
        },
        typeTap(el){//业绩类型
            this.selectFn(el,this.yjtypeList);
            this.data.project.proType=this.forArrStr(this.yjtypeList);
        },
        stateTap(el) {//项目状态
            this.selectFn(el,this.stateList);
        },
        jump(){
            if(this.total==0||this.isNoSee){
                return false
            }
            if(this.isyj||this.data.qualCode!=''||this.data.person.length>0){
                console.info('this.id',this.pkid);
                this.$router.push({
                    path:'/payPage',
                    query: {
                        pkid: this.pkid
                    }
                })
            }else{
                this.$alert('请至少筛选人员，资质，业绩中的一项')
            }
            
        }
    },
    created () {
        this.companyCount();
        this.codeZZ = JSON.parse(localStorage.getItem('filter'));
        let data = JSON.parse(localStorage.getItem('filter'));
        let ryData=JSON.parse(localStorage.getItem('people'));
        this.peopleList=ryData;
        for(let x of data.area){
            x.istap = false
        }
        data.area.unshift({
            areaShortName:'不限',
            istap:true,
        })
        this.projectList = data.area;
    }
};
</script>
<style lang="less">
@import "../../style/publicCSS";
@font-face {
    font-family: "element-icons";
    src: url("//at.alicdn.com/t/font_1546736_e9vb389hamj.eot?t=1575882859526"); /* IE9 */
    src: url("//at.alicdn.com/t/font_1546736_e9vb389hamj.eot?t=1575882859526#iefix")
            format("embedded-opentype"),
        /* IE6-IE8 */
            url("data:application/x-font-woff2;charset=utf-8;base64,")
            format("woff2"),
        url("//at.alicdn.com/t/font_1546736_e9vb389hamj.woff?t=1575882859526")
            format("woff"),
        url("//at.alicdn.com/t/font_1546736_e9vb389hamj.ttf?t=1575882859526")
            format("truetype"),
        /* chrome, firefox, opera, Safari, Android, iOS 4.2+ */
            url("//at.alicdn.com/t/font_1546736_e9vb389hamj.svg?t=1575882859526#iconfont")
            format("svg"); /* iOS 4.1- */
}

.element-icons {
    font-family: "element-icons" !important;
    font-size: 12px !important;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.el-select .el-input__inner {
    color: @lightColor !important;
}
.el-icon-arrow-up:before {
    content: "\e6a1" !important;
}
.el-icon-arrow-up,
.el-icon-arrow-down {
    font-size: @subContentSize !important;
    transform: scale(0.6) !important;
}
.el-select .el-input .el-select__caret {
    color: @darkColor !important;
}
.el-radio__inner:hover {
    border-color: @themeColor !important;
}
.el-radio {
    margin-right: 20px;
}
body .el-radio__input.is-checked+.el-radio__label {
    font-weight: 700;
}
.el-input-number--mini{
    width: 95px;
    .el-input-number__increase,.el-input-number__decrease  {
        background: @pinkColor !important;
        color: @whiteColor !important;
    }
}
.el-input-number__decrease.is-disabled, .el-input-number__increase.is-disabled {
    background-color: @initColor !important;
    color: #C0C4CC !important;
}
.el-input-number__decrease:hover:not(.is-disabled)~.el-input .el-input__inner:not(.is-disabled), .el-input-number__increase:hover:not(.is-disabled)~.el-input .el-input__inner:not(.is-disabled) {
    border-color: #DCDFE6 !important;
}
.el-checkbox__input.is-checked+.el-checkbox__label {
    color: @textColor !important;
}
.el-checkbox__input.is-checked .el-checkbox__inner, .el-checkbox__input.is-indeterminate .el-checkbox__inner {
    background-color:  @themeColor !important;
    border-color: @themeColor !important;
}
.el-checkbox__input.is-focus .el-checkbox__inner, .el-checkbox__inner:hover {
    color: @themeColor !important;
    border-color: @themeColor !important;
}
.el-select-dropdown__item.selected {
    color: @themeColor !important;
}
.el-select .el-input.is-focus .el-input__inner,.el-select .el-input__inner:focus {
    border-color: @themeColor !important;
}
</style>
<style lang="less" scoped>
@import "../../style/publicCSS";
.screenPage {
    .screenPage_body {
        width: 1020px;
        margin: 0 auto;
        .select {
            li {
                color: @lightColor;
                width: 129px;
                height: 44px;
                line-height: 44px;
                text-align: center;
            }
        }
        .select li:nth-last-child(2) {
            width: 2px;
            height: 14px;
            background-color: @contentColor;
            margin: 0 26px;
        }
        .active {
            background-color: @themeColor;
            color: @whiteColor !important;
            border-radius: 3px;
        }
    }
    .screenPage_content {
        border: 1px solid @initColor;
        padding: 20px;
        .nav_top,
        .require {
            border-bottom: 1px solid @initColor;
        }
        .require {
            padding: 20px 0;
        }
        .evaluation {
            padding: 27px 0;
        }
        .scores {
            margin-left: 84px;
        }
        .activeZZ {
            color: @themeColor;
            font-weight: 600;
        }
    }
    .totalBox {
        width: 1020px;
        height: 128px;
        margin: 20px auto 40px;
        background-color: @lightOrangeColor;
        .bottom_img {
            i {
                color: @themeColor;
                font-size: 48px;
            }
        }
        .total {
            font-size: 24px;
        }
        .link_fun {
            .up {
                border-bottom: 1px solid @darkColor;
            }
        }
        .info_btn {
            width: 193px;
            height: 48px;
            border-radius:8px;
            border: 1px solid @themeColor;
            margin-left: 60px;
        }
        .notJump {
            background: #CCC;
            cursor: not-allowed;
            opacity: 0.8;
            border: none;
            color: @whiteColor;
        }
    }
}
</style>


